#root,.cus-sider{height:100%}.ant-layout{background:transparent}.ant-layout .cus-sider{background:rgba(0,21,41,.47);box-shadow:2px 0 6px rgba(0,21,41,.35)}.ant-layout .cus-sider .logo{background:rgba(0,21,41,.47);height:64px;line-height:64px}.ant-layout .cus-sider .logo h1{white-space:nowrap;color:#fff;display:inline-block;vertical-align:middle;font-size:20px;margin-left:20px;font-family:Myriad Pro,Helvetica Neue,Arial,Helvetica,sans-serif;font-weight:600}.ant-layout .cus-sider .menu{background:rgba(0,21,41,.33)}.ant-layout .cus-sider .ant-menu-dark .ant-menu-inline.ant-menu-sub,.ant-layout .cus-sider .sub-menu{background:rgba(0,12,23,.26)}.ant-layout .ant-layout-header{background-color:rgba(0,21,41,.33)}.ant-layout .ant-layout-content{background-color:hsla(0,0%,100%,.8)}.ant-layout .ant-layout-footer{background-color:rgba(240,242,245,.5)}.ant-layout-sider-children{position:fixed;transition:all .2s;width:250px}.ant-layout-sider-collapsed .ant-layout-sider-children{width:0}


